<template lang="pug">
    div(class="charger-info" v-if="chargershow")
            p(class="charger-info__title") 详情
                span(class="chager-info__content" @touchstart.stop="handleTouch") x 
            div.charger-info__item
                span.icon-charger-position 位置
                span {{address}}
            div.charger-info__item
                span.icon-refresh-ui 状态 
                span {{status|textReplace}}
            div.charger-info__item
                span.icon-ev_station 编号
                span {{deviceId}}
            div.charger-info__item
                span.icon-charging-energy 电费
                span {{energyMoney}} 元/度
            div.charger-info__item
                span.icon-charging-service 服务费
                span {{serviceMoney}} 元/度 
            button.charger-info__nav(@click="handleClick") 导航    
</template>
<script>
  export default {
    props:{
        chargershow:{
            required: true,
            type: Boolean,
            default: false
        },
        address:{
            required: true,
            type: String,
            default:'中山远大'
        },
        deviceId:{
            required: true,
            type: Number,
            default: 2
        },
        status:{
            required: true,
            type: Number,
            default: 4
        },
        serviceMoney:{
            required: true,
            type: Number,
            default: 0.1
        },
        energyMoney:{
            required: true,
            type: Number,
            default: 0.1

        },
        mapPosition:{
            required: true,
            type: Array,
            default: function(){
                return [1,1]
            }
        }
    },
    filters: {
      textReplace (status) {
        let condition = ''
         switch(status){
           case 0:
             condition = '电桩离线'
            break;
           case 1:
             condition = '已连接'
            break;
           case 2:
             condition = '充电中'
             break;
           case 3:
             condition = '已预约'
            break
           case 4:
             condition = '空闲'
             break
           default:
             condition = "故障"
         }
         return condition
      }
    },
    components:{
    },
    data(){
        return{
            lng:0,
            lat:0,
            location:''
        }
    },
    methods: {
      handleTouch () {  
        this.$emit('charger-info-touched')
      },
      handleClick(){
        this.lng=this.mapPosition[0]
        this.lat=this.mapPosition[1]
        this.location = this.address
        this.$emit('charger-info-clicked',{lng:this.lng,lat:this.lat,address:this.location}) 
      }
    }
  }
</script>

