<template lang="pug">
    mixin chargingItem(icon,text)
        div.charging__item
            div.charging_hd
                span.charging__item-icon(class=icon)
            div.charging_bd
                p.charging__item-text=text
    div.bg
        div.charging
            +chargingItem('icon-charging-scan','通过微信公众号与电桩获取通讯')
            +chargingItem('icon-user-recharge','付费启动充电')
            +chargingItem('icon-charger-status-idle','实时查看充电进度')
            +chargingItem('icon-charging-bluetooth','远距离操控')
            p(class="charging__logo" @click="scan")
                span.charging__logo-content
            p(class="charging__scan") 扫码充电
            div.charging__btn-hd
                button(@click="handleClick(userStatus)" class="btn btn-primary" v-bind:disabled="Userstatus") 输入编号
            div.charging__btn-ft
                button(@click="handleClick(userStatus)"  class="btn btn-primary"  v-bind:disabled="!Userstatus") 充电状态
</template>
<script>
import {mapState} from 'vuex'
import {USER_STATUS} from './../store/modules/user'
import {STATUS_EVENT}  from '../store/mutation-types'
import {getQueryStringByName} from '../utils/toolFunc.js'
import wx from 'weixin-js-sdk'

export default {
     name: 'charging',
     computed:{
         ...mapState('user',['userStatus']),
         Userstatus(){
             return this.userStatus!==USER_STATUS.LOGIN
         }
     },
     created(){
        if(Object.keys(this.$route.query).length){
            let chargingInfo={
                set_duration: this.$route.query.setDuration,
                set_energy: this.$route.query.setEnergy,
                charger_number: this.$route.query.chargerNumber,
                charging_type: parseInt(this.$route.query.chargingType)
            }
            this.$store.commit('charger/' + STATUS_EVENT.GET_CHARGER_INFO, chargingInfo)
        }
     },
     methods:{
      handleClick(status){
        if(status===USER_STATUS.LOGIN){  
            this.$router.push({path: '/charger-start'})
        }else if(status===USER_STATUS.CHARGING){
            this.$router.push({path: '/charger/status'})
        }
      },   
      scan(){
          var that = this;
          wx.scanQRCode({
              needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果
              scanType: ["qrCode"],
              success : function(res) {
                  let result = res.resultStr
                  let chargerNumber = getQueryStringByName(result,'chargerNumber')
                  that.$router.push({path: '/charger-start', query:{chargerNumber:chargerNumber}})
              },
              error: function(res){
                    if(res.errMsg.indexOf('function_not_exist') > 0){
                        alert('版本过低请升级')
                    }
               }
          })
      },
    }
}
</script>
<style lang="stylus">
@import "./../assets/stylus/common.stylus"
    $whiteColor = #fff
    $blueColor = #0B87CD
    .charging
        width 100%
        margin 0 auto
        padding-top 1rem
        &__item
            display flex
            padding 10px 0 10px 10%
            --webkit-box-align center
            align-items center
            widthHeightLineHeightFontSize(100%, 1.2rem,1.2rem, 1.1rem)
            &-text
                color $whiteColor
        &_bd
            padding-left .5rem 
        &__logo
            margin 13% auto .5rem
            align-items center
            justify-content center
            &-content
                display inline-block
                widthHeightLineHeightFontSize(3rem, 3rem ,0,0)
                border-radius(3rem)
                border 10px solid $blueColor
                background-color $whiteColor
                ^[2]:active
                    box-shadow 0 0 0 5px $whiteColor
        &__scan
            widthHeightLineHeightFontSize(90%, 2rem, 2rem ,1.2rem)
            color $whiteColor
            margin 0 auto 4rem
            position relative
            &:before
                beforeAfter(1px, 34%, $whiteColor, 46.7%, 1, 0)
            &:after
                beforeAfter(1px, 34%, $whiteColor, 46.7%, 0, 0)
        &__btn-hd
            width 90%
            beforeAfter(0, 85%, #52bcf8, 50%, 1, 50%)
            margin-top: 25%
            margin-left: -42%
        &__btn-ft
            width 90%
            beforeAfter(0, 85%, #52bcf8, 50%, 1, 50%)
            margin-top: 40%
            margin-left: -42%    
            
</style>


