import shortId from 'shortid'
import HashIds from 'hashids'

function createMsgId(){
    let hashIds = new HashIds(shortId.generate(),8)
    return hashIds.encode(1)
}
export const msgId = createMsgId()

// 判断一个数是否为整数
export function isInteger(number){
    let obj = parseFloat(number)
    return  typeof obj=== 'number'&& obj!=0 && obj%1===0
}



// 按照 ？ 号截取 url
function splitByqMark(str){
    if(str.indexOf('?')===-1){
       return  'str is not url or not carry parameter'
    }
    return str.split('?')
  }
// 从 url 中根据 name 获取对应参数值
export function  getQueryStringByName(str,name){
    var result = splitByqMark(str)[1]     
    var reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)");       
    var r = result.match(reg);        
    if (r!=null) return r[2]; return '';    
  }