<template lang="pug">
    div.input-number
        div.input-number__hd 请输入充电桩编号
        div.input-number__bd
            span(v-for="(item,index) in chargerNumber" class="input-number__bd-i" v-bind:class="{'input-number__bd-on':Byfilter===index}" @click.stop="handleClick(index)") {{item}}
        base-key-board(v-bind:display="display" v-on:key-board-clicked="handleKeyBoardClick") 
</template>
<script>
import BaseKeyBoard from './BaseKeyBoard'
export default {
    props:{
        display:{
            required: true,
            type: Boolean,
            default:false,
        },
        chargerNumber:{
            required: true,
            type: Array,
            default: [0, 0, 0, 0, 0, 0, 0, 0]
        },
    },
    components:{
        BaseKeyBoard
    },
    data(){
        return {
            Byfilter: 0
        }
    },
    methods:{
        handleKeyBoardClick(val){
            if(val==='del'){
                this.chargerNumber[this.Byfilter]=0
                --this.Byfilter;
                if(this.Byfilter<0){
                    this.Byfilter=this.chargerNumber.length-1;
                }
            }else{
                this.chargerNumber[this.Byfilter] = val;
                ++this.Byfilter;
                if(this.Byfilter>this.chargerNumber.length-1){
                    this.Byfilter=0
                }
            }    
        },
        handleClick(index){
            this.Byfilter=index;
            this.$emit('input-charger-number-clicked',true)
        }
    }
}
</script>
<style lang="stylus" scoped>
    .input-number
        &__hd
            text-indent 5%
            font-size 1rem
            line-height 1.5rem
            padding .5rem 0
            text-align left
            color #888
        &__bd
            width 90%
            display flex
            margin 0 auto 
            &-i
                flex 1
                margin 0 3px
                font-size 1.8rem
                height 2rem
                line-height 2rem
                border 2px solid #a4a4a4
                border-radius 6px
            &-on
                border 2px solid #06ba04

</style>
