<template lang="pug">
div.bg
    div.input-identify
        div(v-on:click="handleInputChargerNumberClicked(false)")
            input-charger-number(:display="display" @input-charger-number-clicked="handleInputChargerNumberClicked" :chargerNumber="chargerNumber")
            charger-message(class="input-identify__charger-info" v-bind="chargerInfo")
            pay-money(v-bind:chargerMode.sync="chargerMode" v-bind:value.sync="value")
        div.input-identify__pay
            div.input-identify__pay_item
                p.input-identify__pay_text 支付金额
                p.input-identify__pay_number {{payForMoney|formatMoney}}
            div.input-identify__pay_item
                p.input-identify__pay_text 账户余额
                p.input-identify__pay_balance {{balance|formatMoney}}
        div.input-identify__btn-hd
                button(@click="payBalance" class="btn btn-primary" v-bind:disabled="enabled") 余额支付
        div.input-identify__btn-ft         
                button(@click="payWechat" class="btn btn-primary"  v-bind:disabled="enabled") 微信支付
</template>

<script>
import {mapState} from 'vuex'
import {formatMoney} from '../filters/formatMoney.js'
import InputChargerNumber from '../components/InputChargerNumber'
import PayMoney from '../components/PayMoney'
import ChargerMessage from '../components/ChargerMessage'
import {CHARGING_TYPE,PAY_TYPE} from '../utils/constants.js'
import {pay} from './../api/pay'
export default {
    name:"InputIdentify",
    components:{
        InputChargerNumber,
        PayMoney,
        ChargerMessage
    },
    filters: {
      formatMoney
    },
    computed:{
        ...mapState('user',{
            balance: state=>state.userInfo.balance,
            userId: state=>state.userInfo.userId,
            openId: state=>state.userInfo.openId,
        }),
        ...mapState('charger',{
            chargerInfo: state=>state.chargerInfo
        }),
        payForMoney(){
            if(this.chargerMode=== CHARGING_TYPE.MONEY){
                return Math.round(this.value);
            }else if(this.chargerMode === CHARGING_TYPE.TIME){
                return Math.round((this.value/60)*this.chargerInfo.devicePower*(this.chargerInfo.serviceRate+this.chargerInfo.energyRate))
            }
        },
        enabled(){
            return !this.value
        }
    },
    watch:{
        display:{
            handler: function (val) {
            var self = this;
            if(!val) {
                this.$store.dispatch('charger/updateChargerInfo',self.chargerNumber.join(''));
               }
            }
        },
        chargerMode:{
            handler:function(val){
                // 每次切换方式时，都需要先置零
                this.value = 0
            }
        }
    },
    data(){
        return {
            chargerNumber: [0, 0, 0, 0, 0, 0, 0, 0],
            display: false,
            chargerMode:0,
            value: 0
        }
    },
    mounted(){
        if(Object.keys(this.$route.query).length){
            let chargerNumber = this.$route.query.chargerNumber.toString()
            for(let i=0;i<this.chargerNumber.length;i++){
                this.chargerNumber[i]=chargerNumber[i]
            }
            this.$store.dispatch('charger/updateChargerInfo',this.chargerNumber.join(''));
        }
        this.$store.dispatch('user/getNewestBalance',{
            userId: this.userId
        })
    },
    methods:{
        handleInputChargerNumberClicked(val){
            this.display = val
        },
        payBalance(){
            if(!this.value){
                return;
            }
             this.$store.dispatch('charger/startCharging',{
                openId: this.openId,
                type: this.chargerMode,
                userId: this.userId,
                chargerNumber: parseInt(this.chargerNumber.join('')),
                value: this.value,
                payType:PAY_TYPE.BALANCE

            })
        },
        payWechat(){
            let openId = this.openId
            let userId = this.userId
            // let rechargeMoney=this.payForMoney
            let rechargeMoney=Math.round(this.payForMoney)
            let that = this;
            // 判断是否可用
            this.$store.dispatch('charger/updateChargerInfo',this.chargerNumber.join('')).then(res=>{
                    pay({
                    openId,
                    rechargeMoney,
                    userId
                }).then(function(res){
                    if(res==='success'){
                        that.$store.dispatch('charger/startCharging',{
                        openId: openId,
                        type: that.chargerMode,
                        userId: userId,
                        chargerNumber: parseInt(that.chargerNumber.join('')),
                        value: that.value,
                        payType:PAY_TYPE.WECHAT
                        })
                    }
                })
            }).catch(error=>{
                console.log(error)
            })
            // 可用再继续支付
        }
    }
}
</script>
<style lang="stylus">
@import "./../assets/stylus/common.stylus"
    .input-identify
        padding .5rem 0
        &__charger-info
            margin-top .3rem
        &__pay
            display flex
            widthHeightLineHeightFontSize(90%, auto ,auto, 1rem)
            margin 0 auto 
            &_item
                flex 1
            &_number
                color #B71313
                font-size .9rem
                padding-top .2rem
        &__btn-hd
            width 90%
            beforeAfter(0, 85%, #52bcf8, 50%, 1, 50%)
            margin-top: 35%
            margin-left: -43%
        &__btn-ft
            width 90%
            beforeAfter(0, 85%, #52bcf8, 50%, 1, 50%)
            margin-top: 48%
            margin-left: -43%
        
</style>


